@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@200;300;400;500;600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Mulish', sans-serif;
  background: linear-gradient(to right, rgb(34, 34, 34), rgb(255, 255, 255));
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.qrcode__container {
  text-align: center;
}

.qrcode__container h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

.qrcode__container p {
  font-size: 16px;
  margin-bottom: 20px;
}

.qrcode__container--parent {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.qrcode__input {
  margin-bottom: 20px;
}

.qrcode__input input {
  width: 300px;
  padding: 10px;
  font-size: 16px;
}

.qrcode__input button,
.qrcode__download button {
  padding: 12px 30px;
  font-size: 16px;
  background-color: rgb(34, 34, 34);
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 5px;
}

.qrcode__image {
  margin-bottom: 20px;
}

.qrcode__image canvas {
  display: block;
}  